@import url('https://fonts.googleapis.com/css2?family=Noto+Sans+KR:wght@100;300;400;500;700;900&display=swap');

* {padding:0; margin:0; transition: transform .3s;}
a {text-decoration:none; color:#333; font-family: 'Noto Sans KR', sans-serif;}
ul,ol,li {list-style: none;}
body {font-size: 16px; color:#333; font-family: 'Noto Sans KR', sans-serif;}


/* 3.게시판 */

#article_main {
  width: 1100px;
  height: 650px;
  margin: 0 auto;
  background-color: #fff;
  position: relative;
}

#article_top {
  margin: 20px 0;
  overflow: hidden;
}
#article_top #top_text div:nth-child(1) {
  font-size: 22px;
  font-weight: bold;
}
#article_top #top_text div:nth-child(2) {
  font-size: 13px;
}


#notice_board {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-content: center;
  align-self: center;
  text-align: center;
  position: relative;
  border-top: 2px solid #333;
  font-size: 14px;
}
#notice_board .board_row {
  display: flex;
  height: 40px;
  line-height: 40px;
  font-size: 16px;
  border-bottom: 2px solid #c2c1c1;
  background-color: #f8f8f8;
}

