  width: 100px;
}
.delete_button {
  width: 70px;
  margin: 7px auto;
  font-size: 12px;
  z-index: 99;
  color: #df1313;
}

#pagination {
  display: flex;
  justify-content: center;
  position: absolute;
  bottom: 15px;
  left: 38.5%;
}
.button_down {
  width: 30px;
  height: 30px;
  margin: 30px 0;
  color: #9e9e9e;
  background-color: #fff;
  border: 1px solid gray;
}
.button_down:hover {
  background-color: #c2c1c1;
}
.button_down:nth-child(1) {
  background-color: #c2c1c1;
  color: #333;
  font-weight: lighter;
}
.before_move {
  width: 50px;
  height: 30px;
  margin: auto 5px;
  background-color: #fff;
  border: 1px solid gray;
}
.before_move:hover {
  background-color: #c2c1c1;
}
.next_move {
  width: 50px;
  height: 30px;
  margin: auto 5px;
  background-color: #fff;
  border: 1px solid gray;
}
.next_move:hover {
  background-color: #c2c1c1;
}

#post_write {
  float: right;
  border: 1px solid #333;
  margin: 0 30px;
  height: 30px;
  line-height: 30px;
  background-color: #4588c7;
}
#post_write a {
  padding: 0 20px;
  color: #fff;
}
#post_write:hover {
  background-color: #c2c1c1;
}
#post_write:hover a{
  color: #333;
}

#form_post_black {
  height: 100%;
  width: 100%;
  background-color: #333333d3;
  position: absolute;
  top: 0;
  left: 0;
  z-index: 100;
  display: none;
}

#form_post {
  width: 800px;
  margin: 150px auto;
  padding: 30px;
  background-color: #fff;
}
#form_post #top_text {
  margin-bottom: 20px;
}
#form_post #top_text div:nth-child(1) {
  font-size: 22px;
  font-weight: bold;
}
#form_post #top_text div:nth-child(2) {
  font-size: 13px;
}

#form_post input {
  height: 25px;
  width: 667px;
  margin: 0 30px;
}
#form_post textarea {
  height: 150px;
  width: 650px;
  padding: 10px;
  margin: 0 68px;
}

#my_form button {
  width: 80px;
  height: 30px;
  background-color: #333;
  color: #fff;
  margin-left: 310px;
}
#my_form button:hover {
  background-color: #c2c1c1;
  color: #333;
}
#my_form div:nth-child(1) {
  height: 50px;
  padding: 10px 0;
  box-sizing: border-box;
  border-top: 2px solid #333;
}
#my_form div:nth-child(2) {
  height: 50px;
  padding: 10px 0;
  box-sizing: border-box;
  border-top: 1px solid #c2c1c1;
}
#my_form div:nth-child(2) span {
  margin: 0 15px;
}
#my_form div:nth-child(3) {
  height: 50px;
  padding: 10px 0;
  box-sizing: border-box;
  border-top: 2px solid #333;
}

#cancle_button {
  width: 78px;
  height: 28px;
  line-height: 28px;
  text-align: center;
  font-size: 14px;
  border: 1px solid #333;
  display: inline-block;
  margin: 0 5px;
}
#cancle_button:hover {
  background-color: #c2c1c1;
}
.stopscroll {
  overflow:hidden;
}


/* 1)components - NavBar */
#article_top #top_button {
  display: flex;
  justify-content: center;
  margin-top: 20px;
  /* border-top: 1px solid rgb(207, 207, 207); */
}
#article_top #top_button li {
  border: 1px solid #adadad;
  width: 150px;
  height: 40px;
  text-align: center;
  line-height: 40px;
  margin: 0 5px; 
}
#article_top #top_button li:hover {
  background-color: #dfdfdf;
}

.active1 {
  background-color: #333;
  color: #fff;
}

